let AnimationMnagementCategoryComponent = class AnimationMnagementCategoryComponent {
    constructor(service, router, spinner) {
        this.service = service;
        this.router = router;
        this.spinner = spinner;
        this.limit = 10;
        this.page = 1;
        this.p = 0;
        this.animationdata = [];
        this.loading = false;
    }
    ngOnInit() {
        this.animation();
    }
    animation() {
        this.spinner.show();
        this.animationdata = [];
        let object = {
            "search": this.searchname,
            "page": this.page,
            "limit": this.limit,
        };
        this.service.postApi('admin/animationCategoryList', object, 1).subscribe(res => {
            console.log('resdfdsfsdfdss==>>', res);
            if (res.body.response_code == 200) {
                this.spinner.hide();
                this.animationdata = res.body.result.docs ? res.body.result.docs : res.body.result;
                this.total = res.body.result.total;
            }
            else {
                this.spinner.hide();
            }
        }, error => {
            this.spinner.hide();
        });
    }
    Searchvaluechange(searchdata) {
        this.searchname = searchdata;
        this.page = 1;
        this.animation();
    }
    reset() {
        this.searchname = '';
        this.search = '';
        this.animation();
    }
    changePage(page) {
        this.page = page;
        this.p = page - 1;
        this.animation();
    }
    deleteId(id) {
        this.videoDeleteId = id;
        $('#modalDelete').modal({ backdrop: 'static', keyboard: false });
    }
    deleteFunctions() {
        let object = {
            "categoryId": this.videoDeleteId
        };
        this.service.postApi('admin/deleteAnimationCategory', object, 1).subscribe(res => {
            if (res.body.response_code == 200) {
                this.service.showSuccess(res.body.response_message);
                this.animation();
            }
            else {
                this.service.toastErr(res.body.response_message);
            }
        }, error => {
        });
    }
    block(_id) {
        this.block_id = _id;
        console.log('block_id', this.block_id);
        $('#modalblock').modal({ backdrop: 'static', keyboard: false });
    }
    blockFunction() {
        let object = {
            "categoryId": this.block_id
        };
        this.service.postApi('admin/blockUnblockAnimationCategory', object, 1).subscribe(res => {
            console.log('res_block');
            if (res.body.response_code == 200) {
                this.service.showSuccess(res.body.response_message);
                this.animation();
            }
            else {
                this.service.toastErr(res.body.response_message);
            }
        }, error => {
        });
    }
    unblock(id) {
        this.unblock_id = id;
        console.log('unblock_id', this.unblock_id);
        $('#modalunblock').modal({ backdrop: 'static', keyboard: false });
    }
    unblockFuntion() {
        let object = {
            "categoryId": this.unblock_id
        };
        this.service.postApi('admin/blockUnblockAnimationCategory', object, 1).subscribe(res => {
            console.log('unblock_res', res);
            if (res.body.response_code == 200) {
                this.service.showSuccess(res.body.response_message);
                this.animation();
            }
            else {
                this.service.toastErr(res.body.response_message);
            }
        });
    }
};
